/* Review css */


.reviewtop{
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    padding: 1%;
}

.customerid{
   
    font-size: 12px;
}
.bestrate{
    float: right;
}
.secdiv{
    padding: 1%;
    
}

.best{
    font-size: 14px;
    float: left;
    font-size: 18px;
    font-weight: 600;
}


.bigbox{
    padding: 8%;
    background-color: #EAF3F9;
}

.smallbox{
    background-color: white;
    border-radius: 12px;
    padding-bottom: 4%;
}

#review-card{

   
    background: white;
   border-color: #2DD1DB;
   border-style: solid;
   border-width: 0.1rem;

/* box-shadow: 0px 12px 24px 4px rgba(10, 61, 89, 0.3); */
border-radius: 12px;
padding: 1%;
height:30vh;
width: 100%;
max-height: 50vh !important;

}

.reviewbtn{
    display: flex;
    justify-content: center;
    padding-top: 2%;
}

.morereview{
    border: none;
    text-decoration: underline !important;
    font-size: 16px;
    background: linear-gradient(232.68deg, #2DD1DB -8.55%, #2D9CDB 57.36%, #1A8ED0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

